<template>
  <Early v-if="clickedTooEarly"></Early>
  <header>
    <h1> TimerZ </h1>
  </header>
  <p>Check your reaction time !</p>
  <p>Click button to start, wait for square to turn red and click it as fast as you can !</p>

  <Result></Result>

  <button @click="starting" ref="xd"> Start Game </button>

  <Square :class="normalState ? 'green' : 'red'"></Square>
</template>

<script>
import Result from './components/Result.vue'
import Square from './components/Square.vue'
import Early from './components/Early.vue'

export default {
  name: 'App',
  components: {
    Result,
    Square,
    Early
  },
  data () {
    return {
      normalState: true,
      timeOfClick: 0,
      differenceMs: 0,
      isFinished: false,
      clickedTooEarly: false
    }
  },
  methods: {
    starting () {

      this.$refs.xd.setAttribute('disabled', 'disabled')

      let x = parseFloat((Math.random() * 5000 + 2000 ).toFixed(2))

      setTimeout (() => {
        this.normalState = false
        const y = new Date;
        this.timeOfClick = y.getTime();
      }, x)
    },
    again () {
            window.location.reload()
    }
  }
}
</script>

