<template>
    <div class="square" @click.once="checkResult"></div>
</template>

<script>
export default {
    data () {
        return {
            
        }
    },
    methods: {
        checkResult(event) {
            if(event.target.classList.contains('green')){
                this.$parent.clickedTooEarly = true
            } else {
                let x = new Date;
                console.log(x.getTime() - this.$parent.timeOfClick)
                this.$parent.differenceMs = x.getTime() - this.$parent.timeOfClick
                this.$parent.isFinished = true
            }
        }
    }
}
// parseFloat((Math.random() * 6 + 2).toFixed(2))
</script>

<style lang="scss">
    .square {
        width: 200px;
        height: 200px;
        border-radius: 10px;
        margin-inline: auto;
        margin-top: 2rem
    }

    .green {
        background: green;
    }

    .red {
        background: red;
    }
</style>